.overlay {
  height: 0%;
  width: 100%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.9);
  overflow-y: hidden;
  transition: 0.5s;
  z-index: 100;
}

.overlay-content {
  position: relative;
  top: 11%;
  width: 100%;
  text-align: center;
}

.overlay-content h2 {
  color: black;
  margin-bottom: 77px;
}

